export default {
  name: 'RiliTable',
  data() {
    return {
      title: ["", "", "", "", "", "", ""],
      days: []
    }
  },
  props: {
    time: {
      type: Date,
      require: true
    },
    specificDates: {
      type: Array,
    },
    specificColors: {
      type: Object
    }
  },
  computed: {
    /**
     * 
     * @return {number}   0-60
     */
    getFirstDayIsWeek: function () {
        return new Date(this.time.getFullYear(), this.time.getMonth(), 1).getDay()
    },
    /**
     * 
     * @return {Number} 
     */
    getMonthSumDay: function() {
        return new Date(this.time.getFullYear(), this.time.getMonth()+1, 0).getDate()
    },
  },
  methods: {
    /**
     * top
     * @param  {number} index 
     * @return {number}       
     */
    getTop: function(index) {
      return index * 0.58
    },
    /**
     * 027
     * @param  {Number} weekDay 
     * @return {number}         
     */
    changeWeekDay: function(weekDay) {
      return weekDay?weekDay:7
    },
    /**
     * 
     */
    pushEveryDay: function() {
      // , type[array]
      for (let end = 6, top = 7 - this.getFirstDayIsWeek; end >= 0; end--, top--) {
        this.days[end] = {val: top>0?top:'', type: []}
        for(let i = 0, len = this.specificDates.length; i < len;i++) {
          if(top === new Date(this.specificDates[i].time).getDate()) {
            this.days[end].type.push(this.specificDates[i].type)
          }
        }
      }
      // type[Array]
      for (let i = 7 - this.getFirstDayIsWeek + 1; i <= this.getMonthSumDay;i++) {
        this.days.push({val: i, type: []})
        for(let j = 0, len = this.specificDates.length; j < len;j++) {
          if(i === new Date(this.specificDates[j].time).getDate()) {
            this.days[this.days.length - 1].type.push(this.specificDates[j].type)
          }
        }
      }
      // 7
      if((this.getMonthSumDay - (7 - this.getFirstDayIsWeek)) % 7 !== 0) {
        for(let len = 7 - (this.getMonthSumDay - (7 - this.getFirstDayIsWeek)) % 7; len > 0; len--) {
          this.days[this.days.length] = {val: '', type: []}
        }
      }
    }
  },
  watch: {
    /**
     * specificDates
     */
    specificDates: function () {
      this.days = []
      this.pushEveryDay()
    }
  },
  created() {
    this.pushEveryDay()
  }
}



